    // 发送消息-实现
    handleSendMsgImpl(topRes, sendCnt) {
      const self = this

      console.log('发送前查询用户消息')
      // 查询对方用户信息
      self.$http.imUserGet({
        id: topRes.data
      }).then(result => {
        console.log('准备跳转')
        uni.hideLoading()
        app.globalData.Services.Message.sendRegister(function(resInfo) {
          self.$openPage({ name: 'chat', query: { targetId: topRes.data + '', avatarUrl: result.data.userPortrait, userName: result.data.userName }})
        }, {
          targetId: topRes.data + '',
          content: sendCnt
        }, {
          id: result.data.userId,
          portrait: result.data.userPortrait,
          name: result.data.userName,
          meid: self.userInfo.userId,
          meportrait: self.userInfo.avatarUrl,
          mename: self.userInfo.nickName,
          companyName: self.memberInfo.companyName
        })
      })
    },

    imCheckOnline(id) {
      const self = this
      self.$http
        .imCheckOnline({
          id
        })
        .then(res => {
          console.log('是否在线')
          if (res.status !== '1') {
            console.log('对方不在线')
            self.$http
              .sendSubscribeMsg({
                toUserId: id,
                message: '你有一条新消息！'
              })
              .then(res => {
                console.log('小程序订阅消息发送成功')
              })
              .catch(err => {
                console.log(err)
              })
          }
        })
        .catch(err => {
          console.log(err)
        })
    },

    // 后端埋点 告知后端推送订阅消息
    chatSubscribe() {
      const self = this
      self.$http
        .weChatSubscribe({})
        .then(res => {
          self.userInfo.isSubscribe = 1
          self.$store.commit('setUserInfo', this.userInfo)
        })
        .catch(err => {
          console.log(err)
        })
    },

    // 拨打电话
    handleCallPhone(phone) {
      uni.showModal({
        title: '提示',
        content: '是否拨通联系电话' + phone,
        success: function(res) {
          if (res.confirm) {
            uni.makePhoneCall({
              phoneNumber: phone // 仅为示例
            })
          } else if (res.cancel) {
            console.log('用户点击取消')
          }
        }
      })
    },

    // 收藏、取消收藏
    handleCollect(e) {
      const self = this
      console.log(this.loginStatus)
      if (this.loginStatus !== 0) {
        this.$http
          .quoteLike({
            quoteId: this.quoteId,
            type: 2
          })
          .then(res => {
            self.isCollect = !self.isCollect
          })
          .catch(err => {
            console.log(err)
          })
      } else {
        this.wxUserLogin(e,'collect')
      }
    },

    // 点赞、取消点赞
    handleSupport(e) {
      console.log(this.loginStatus)
      const self = this

      if (this.loginStatus !== 0) {
        this.$http
          .quoteLike({
            quoteId: this.quoteId,
            type: 1
          })
          .then(res => {
            if (self.isLike) {
              self.exampleInfo.likeNum -= 1
            } else {
              self.exampleInfo.likeNum += 1
            }
            self.isLike = !self.isLike
          })
          .catch(err => {
            console.log(err)
          })
      } else {
        this.wxUserLogin(e,'support')
      }
    },

    /**
       * 微信用户授权
       * @param {Object} e
       */
    getUserInfo(e,type) {
      console.log('用户拉取授权')
      
      if (e.detail && e.detail.rawData) {
        this.wxUserLogin(e, type)
      } else {
        uni.showModal({
          title: '提示',
          content: Messages.APPLY_AUTH,
          showCancel: false,
          confirmColor: '#fa5151'
        })
      }
    },

    /**
       * 微信用户授权登录
       * @param {Object} detail
       */
    wxUserLogin(e, type) {
      	const that = this

      	uni.login({
      		provider: 'weixin',
      		success: function(loginRes) {
          wx.getUserInfo({
            	success: (user) => {
            			const params = {
            				code: loginRes.code,
            				rawData: user.rawData,
            				signature: user.signature,
            				encryptedData: user.encryptedData,
            				iv: user.iv
            			}
                  that.$http.login(params).then(res => {
                  	if (res && res.code == 200) {
                  		const userInfo = {
                  			avatarUrl: res.data.avatarUrl,
                  			nickName: res.data.nickName,
                  			isSubscribe: res.data.isSubscribe,
                  			userId: res.data.userId
                  		}
                  		that.avatarUrl = userInfo.avatarUrl
                  		that.nickName = userInfo.nickName
                  		that.$store.commit('setToken', res.data.token)
                  		that.$store.commit('setUserInfo', userInfo)
                  		that.$store.commit('setLoginStatus', 1)
                  		// that.initSDK(that)

                  if (type === 'support') {
                    that.handleSupport()
                  } else if (type === 'collect') {
                    that.handleCollect()
                  }
                  	}
              })
            	}
          })
      		}
      	})
    },

    initSDK(context) {
      	const self = this

      	const appKey = context.data.appKey
      	const token = context.data.token

      	if (appKey == '' || token == '') {
      		// return showPrompt('必须提供 appKey 和 token');
      	}

      	this.$http
      		.imUserRegister({
      			id: self.userInfo.userId,
      			name: self.userInfo.nickName,
      			portraitUri: self.userInfo.avatarUrl
      		})
      		.then(res => {
      			console.log(res)

      			// 公有云初始化
      			const modules = {
      				// protobuf: 'https://cdn.ronghub.com/protobuf-2.3.1.min.js' //支持http(s)网络路径、本地相对路径,
      				RongIMLib: RongIMLib,
      				protobuf: null
      			}

      			const params = {
      				appKey: appKey,
      				token: token,
      				connectToken: res.data.token
      			}

      			const callbacks = {
      				receiveNewMessage: function(message) {
      					uni.$emit('newMessage', message)
      				},
      				getCurrentUser: function(userInfo) {
      				},
      				onTokenIncorrect: function() {
      				},
      				watchConversationList: function(list) {
      				}
      			}
      			app.globalData.Services.init(params, callbacks, modules)
      		})
      		.catch(err => {
      			console.log(err)
      		})
    },

    // 跳转out外网桥梁页面
    handleGoOut(url) {
      this.$openPage({ name: 'out', query: { url }})
    },

    // 预览图片
