          success: function(data) {},
          fail: function(err) {
            console.log(err.errMsg)
          }
        }
      })
    },

    handleDynamicOneFunc() {
      // console.log("更改状态")

      this.navList = [{
        state: 0,
        text: '工地实况',
        loadingType: 'more',
        orderList: []
