        orderList: []
      },
      {
        state: 2,
        text: '材料品牌',
        loadingType: 'more',
        orderList: []
      }]

      this.isComplete = false

      uni.setNavigationBarTitle({
        title: '工地实况'
      })

      // console.log(this.navList)
    },

    openShopLocation(latitude, longitude) {
      uni.openLocation({
        latitude: parseFloat(latitude),
        longitude: parseFloat(longitude)
      })
    }

